	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"First Day of School","date":"25 Aug 2000","slug":"/2000-08-25-first-day-of-school/","tags":"Y2000"},"html":"<p>Mama's nephew (and godson) Lennart starts first grade tomorrow. In Germany, starting first grade is a Very Big Deal, the first step in coming-of-age. The new <i>ABC Schutzen</i> (ABC shooters) are celebrated with a <i>Schultüte</i> full of candy and goodies, a brand new school bag (<a href=\"http://www.der-echte-scout.de\">name brands</a> start at DM 150), and a party for selected relatives and guests.</p>\n<p>Lennart's school has thoughtfully made the first day a Saturday, so that the selected relatives and guests have no excuse not to participate. The festivities begin with a church service at 9 am, and continue through to the obligatory coffee and cake in the late afternoon.</p>\n<p>The net result is that even the not-so-interested spouse of the godmother has to block out an entire Saturday from his calendar.</p>\n<p>When I started first grade, I might have gotten an apple, but probably not. I think my mother was just happy to finally have me out of her hair for the whole day. </p>\n<p>German elementary schools are only half day, by the way. That makes life complicated or working parents.</p>"},"next":{"fields":{"title":"Gateway Poster Child","date":"23 Aug 2000","slug":"/2000-08-23-gateway-poster-child/"}},"previous":{"fields":{"title":"Christopher's first ride","date":"29 Aug 2000","slug":"/2000-08-29-christophers-first-ride/"}}},{"node":{"fields":{"title":"Gateway Poster Child","date":"23 Aug 2000","slug":"/2000-08-23-gateway-poster-child/","tags":"Y2000"},"html":"<p><img src=\"http://www.papascott.de/wordpress/wp-content/uploads/2000/08/poster.jpg\" height=\"350\" width=\"279\" border=\"0\" alt=\"gateway.jpg: \" /></p>"},"next":{"fields":{"title":"First Words","date":"19 Aug 2000","slug":"/2000-08-19-first-words/"}},"previous":{"fields":{"title":"First Day of School","date":"25 Aug 2000","slug":"/2000-08-25-first-day-of-school/"}}},{"node":{"fields":{"title":"First Words","date":"19 Aug 2000","slug":"/2000-08-19-first-words/","tags":"Y2000"},"html":"<p><i>For quite some time Christopher's favorite seemed to be \" Da Da Da\".  I couldn't figure out what he meant by saying that. He must have noticed because he switched to saying \"Mum Mum Mum\". Usually he says that right before, during or after his  meals. Maybe he's learned that his Mom has a lot do with his meals.</i></p>\n<p><i>Christopher is also starting to enjoy his weekly  bath.</i></p>\n<p>\"bath\"</p>\n<p><i>He splashes so much water around that everyone gets wet!</i></p>"},"next":{"fields":{"title":"Full coverage","date":"18 Aug 2000","slug":"/2000-08-18-full-coverage/"}},"previous":{"fields":{"title":"Gateway Poster Child","date":"23 Aug 2000","slug":"/2000-08-23-gateway-poster-child/"}}}],"pathPrefix":"","first":false,"last":false,"index":1045,"pageCount":1101}}

